import { request } from '../../request.js'
export default {
	data() {
		return {
			// 用户命
			username: '',
			// 密码
			code: '',
			// 手机号
			cellphone: '',
			// 手机验证码
			vertification: '',
			// 控制按钮是否可以点击
			disabled: false,
			get: true,
			down: false,
			again: false,
			// 倒计时秒数
			count: 60,
			uid:0,
			// 存储第一次屏幕的可视高度
			clientHeight: '',
			tabbar: true
		};
	},
	onLoad(ob){
		if(ob.uid >0) {
			this.uid = ob.uid;	
		}else{
			this.uid= 0;
		}
		// 储存第一次屏幕可视区高度
		uni.getSystemInfo({
			success: res => {
				console.log(res.windowHeight);
				this.clientHeight = res.windowHeight
			}
		})
		// 读取屏幕可视区域高度变化
		uni.onWindowResize((res) => {
        if(res.size.windowHeight < this.clientHeight){
            this.tabbar = false
        }else{
            this.tabbar = true
        }
    })
		
	},
	methods: {
		// 跳转到登录页
		login() {
			if(this.uid>0){
				uni.redirectTo({
					url: '../login/login?uid='+this.uid
				})
			}else{
				uni.redirectTo({
					url: '../login/login'
				})
			}
		},
		// 检测用户名
		user() {
			let test = /[\u4E00-\u9FA5\uF900-\uFA2D]/.test(this.username);
			if(test || this.username == '') {
				uni.showToast({
					title: '请输入合法用户名',
					icon: 'none'
				})
				return
			}
		},
		// 检测密码
		password() {
			let test = /[\u4E00-\u9FA5\uF900-\uFA2D]/.test(this.code);
			if(test || this.code == '') {
				uni.showToast({
					title: '请输入合法密码',
					icon: 'none'
				})
			}
		},
		// 检测手机号码是否注册过
		async phone() {
			if(this.cellphone == '' || !(/^1[3456789]\d{9}$/.test(this.cellphone))) {
				uni.showToast({
					title: '请输入合法的手机号',
					icon: 'none'
				})
				return false
			}
			await request({
				url: 'index.php?s=/wap/login/checkMobileIsHas',
				method: 'post',
				data: {
					mobile: this.cellphone
				}
			}).then(res => {
				if(res.data) {
					uni.showToast({
						title: '手机号已被注册',
						icon:'none'
					});
					
				}
			})
		},
		// 检测验证码
		QRcode() {
			if(this.vertification == '') {
				uni.navigateTo({
					title: '请输入合法验证码',
					icon: 'none'
				})
			}
		},
		// 发送验证码
		getCode() {	
			if(this.cellphone != '' && (/^1[3456789]\d{9}$/.test(this.cellphone))) {
				uni.showToast({
					title: '短信发送成功',
					icon: 'none'
				});
				this.disabled = true;
				this.get = false;
				this.down = true;
				this.again = false
				let timer = setInterval(() => {
					this.count = this.count-1;
					if(this.count <= 0) {
						this.disabled = false;
						this.down = false;
						this.again = true;
						this.count = 5;
						clearInterval(timer)
					}
				},1000)
					request({
						url: '/index.php?s=/wap/Login/sendSmsRegisterCode',
						method: 'post',
						data: {
							vertification: 123,
							mobile: this.cellphone
						}
					}).then(res => {
						
					})
			}
			else {
				uni.showToast({
					title: '请输入合法手机号',
					icon: 'none'
				})
			}
		},
		// 点击注册
		register() {
			request({
				url: 'index.php?s=/wap/Login/register',
				method: 'post',
				data: {
					username: this.username,
					password: this.code,
					mobile: this.cellphone,
					vertification: this.vertification,
					pid:this.uid
				}
			}).then(res => {
				
				if(res.data.retval > 0) {
					uni.showToast({
						title: '注册成功',
						icon: 'none'
					})
					uni.switchTab({
						url: '/pages/mine/mine'
					})
				}
				if(res.data.code > 0) {
					uni.showToast({
						title: '注册成功',
						icon: 'none'
					})
					uni.switchTab({
						url: '/pages/mine/mine'
					})
				}
				else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},
	}
}
